import React from 'react';

const Bloglist = (props) => {
    return (
        <>
         <div className="row align-items-center  blog-items-row mb-30">
            <div className="col-lg-6">
                <div className="feature-thumb">
                    <a href="#">
                        <img src={props.blogimg} alt="Blog"/>
                    </a>
                </div>
            </div>
            <div className="col-lg-6">
                <div className="single-blog">
                    <div className="blog-info">
                        <p><a href={props.adminlink}>{props.postman}</a> / <a href={props.commentlink}>{props.commontlabel}</a></p>
                    </div>
                    <h3 className="text-uppercase"><a href="#"></a>{props.title}</h3>
                    <div className="desc">
                        <p>{props.blogdesc}</p>
                        <a className="redmore" href={props.linkbtn}>{props.buttonlabel}</a>
                    </div>
                </div>
            </div>
        </div>   
        </>
    )
}

export default Bloglist
