import React from 'react'

const Casestudyitem = (props) => {
    return (
        <>
            <div className="col-lg-4">
                <div className="case-study-single">
                    <img src={props.cimag} alt="Case study"/>
                    <div className="sigle-case-study-meta">
                        <a href={props.clink}>{props.ctitle}</a>
                        <div className="category">
                            <p>{props.ccategory}</p>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Casestudyitem
