import React from 'react'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import Sliderimage1 from "../assets/img/slider/1.png";
import Sliderimage2 from "../assets/img/slider/2.png";
import Sliderimage3 from "../assets/img/slider/3.png";
import Sliderimage4 from "../assets/img/slider/4.png";
import Sliderimage5 from "../assets/img/slider/5.png";
import Sliderimage6 from "../assets/img/slider/6.png";
import Sliderimage7 from "../assets/img/slider/6.png";
import Sliderimage8 from "../assets/img/slider/7.png";

const Logo = () => {
    const settings = {
        
            dots: false,
            infinite: false,
            speed: 500,
            slidesToShow: 4,
            slidesToScroll: 4,
            initialSlide: 0,
            responsive: [
              {
                breakpoint: 1024,
                settings: {
                  slidesToShow: 3,
                  slidesToScroll: 3,
                  infinite: true,
                  dots: true
                }
              },
              {
                breakpoint: 600,
                settings: {
                  slidesToShow: 2,
                  slidesToScroll: 2,
                  initialSlide: 2
                }
              },
              {
                breakpoint: 480,
                settings: {
                  slidesToShow: 1,
                  slidesToScroll: 1
                }
              }
            ]
          
    };

    return (
        <>
          <div className="logo-area">
              <div className="container">
                  <div className="logo-outer-area">
                    <div className="row">
                        
                            <Slider {...settings}>
                                <div className="col">
                                    <div className="logo-outer d-flex align-items-center">
                                        <div className="logo-inner-area text-center">
                                                <img src={Sliderimage1} alt="logo"/>
                                        </div>
                                    </div>
                                </div>
                                <div className="col">
                                    <div className="logo-outer d-flex align-items-center">
                                        <div className="logo-inner-area text-center">
                                                <img src={Sliderimage2} alt="logo"/>
                                        </div>
                                    </div>
                                </div>
                                <div className="col">
                                    <div className="logo-outer d-flex align-items-center">
                                        <div className="logo-inner-area text-center">
                                                <img src={Sliderimage3} alt="logo"/>
                                        </div>
                                    </div>
                                </div>
                                <div className="col">
                                    <div className="logo-outer d-flex align-items-center">
                                        <div className="logo-inner-area text-center">
                                                <img src={Sliderimage4} alt="logo"/>
                                        </div>
                                    </div>
                                </div>
                                <div className="col">
                                    <div className="logo-outer d-flex align-items-center">
                                        <div className="logo-inner-area text-center">
                                                <img src={Sliderimage5} alt="logo"/>
                                        </div>
                                    </div>
                                </div>
                                <div className="col">
                                    <div className="logo-outer d-flex align-items-center">
                                        <div className="logo-inner-area text-center">
                                                <img src={Sliderimage2} alt="logo"/>
                                        </div>
                                    </div>
                                </div>

                                <div className="col">
                                    <div className="logo-outer d-flex align-items-center">
                                        <div className="logo-inner-area text-center">
                                                <img src={Sliderimage6} alt="logo"/>
                                        </div>
                                    </div>
                                </div>
                                

                                <div className="col">
                                    <div className="logo-outer d-flex align-items-center">
                                        <div className="logo-inner-area text-center">
                                                <img src={Sliderimage7} alt="logo"/>
                                        </div>
                                    </div>
                                </div>
                                

                                <div className="col">
                                    <div className="logo-outer d-flex align-items-center">
                                        <div className="logo-inner-area text-center">
                                                <img src={Sliderimage8} alt="logo"/>
                                        </div>
                                    </div>
                                </div>
                                
                            </Slider>
                        
                    </div>
                  </div>
              </div>
          </div>  
        </>
    )
}

export default Logo
