import React from 'react'

const Bloglist2 = (props) => {
    return (
        <>
            <div className="row align-items-center blog-items-row mb-30">
                <div className="col-lg-6 order-lg-2">
                    <div className="feature-thumb">
                        <a href="#">
                            <img src={props.blogimg2} alt="Blog"/>
                        </a>
                    </div>
                </div>
                <div className="col-lg-6 order-lg-1">
                    <div className="single-blog">
                        <div className="blog-info">
                            <p><a href={props.adminlink2}>{props.postman2}</a> / <a href={props.commentlink2}>{props.commontlabel2}</a></p>
                        </div>
                        <h3 className="text-uppercase"><a href="#"></a>{props.title2}</h3>
                        <div className="desc">
                            <p>{props.blogdesc2}</p>
                            <a className="redmore" href={props.linkbtn2}>{props.buttonlabel2}</a>
                        </div>
                    </div>
                </div>
                
            </div>   
        </>
    )
}

export default Bloglist2
