import React from 'react'

const Whybox = (props) => {
    return (
        <>
           <div className="why-box d-flex align-items-center">
                <div className="icon">
                    <i className={props.icon}></i>
                </div>
                <div className="desc">
                    <p className="title">{props.title}</p>
                    <div className="long-desc"><p>{props.desc}</p> </div>
                </div>
            </div>  
        </>
    )
}

export default Whybox
