import React from 'react'
import { NavLink } from 'react-router-dom'
const Header = () => {
    return (
        <div className="header-area">
            <div className="header-top-area">
                <div className="container">
                    <div className="header-top-outer-row">
                        <div className="row">
                            <div className="col-lg-6">
                                <div className="header-top-left-content">
                                    <p><i className="fa fa-comments"></i> Need free consultation <a href="#">Book Schedule Now</a></p>
                                </div>
                            </div>
                            <div className="col-lg-6">
                                <div className="header-top-right-content text-right">
                                    <ul className="text-right">
                                        <li><a href="#"><i className="fa fa-facebook"></i></a></li>
                                        <li><a href="#"><i className="fa fa-behance"></i></a></li>
                                        <li><a href="#"><i className="fa fa-twitter"></i></a></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="header-menubar">
                <nav className="navbar navbar-expand-lg">
                    <div className="container">
                        <NavLink className="navbar-brand" exact to="/">Finxer</NavLink>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                        </button>
                        <div class="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav mb-2 mb-lg-0">
                                <li className="nav-item">  <NavLink exact activeClassName="active" className="nav-link" to="/">Home</NavLink></li>
                                <li className="nav-item">  <NavLink exact activeClassName="active" className="nav-link" to="/about">About</NavLink></li>
                                <li className="nav-item">  <NavLink exact activeClassName="active" className="nav-link" to="/service">Service</NavLink></li>
                                <li className="nav-item">  <NavLink exact activeClassName="active" className="nav-link" to="/contact">Contact</NavLink></li>
                                <li className="nav-item">  <NavLink exact activeClassName="active" className="nav-link" to="/Blog">Blog</NavLink></li>
                            </ul> 
                            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">                              

                                <li className="nav-item talkto"><NavLink exact activeClassName="active" className="nav-link" to="/contact"><i className="fa fa-phone"></i> <span>Talk to an expert</span></NavLink></li>
                                <li className="nav-item contactus"><NavLink exact activeClassName="active" className="nav-link" to="/contact">Contact</NavLink></li>
                            </ul> 
                        </div>
                    </div>
                    </nav>
            </div>
        </div>
    )
}

export default Header
