import React from 'react'

const Serviceitem = (props) => {
    return (
        <>
           <div className="col-lg-4">
                <div className="single-service-solution text-center">
                    <div className="count">
                        <span>{props.scountnumb}</span>
                    </div>
                    <div className="icon">
                        <i className={props.icon}></i>
                    </div>
                    <div className="title">
                        <p>{props.title}</p>
                    </div>
                    <div className="desc">
                        <p>{props.desc}</p>
                    </div>
                    <div className="readmore">
                        <a href={props.slink}><i className="fa fa-plus"></i></a>
                    </div>
                </div>
            </div> 
        </>
    )
}

export default Serviceitem
