import React from 'react'

const Breadcum = (props) => {
    return (
        <div className="breadcum-area">
            <div className="container">
                <div className="row">
                    <div className="bredcum-inner-content">
                        <h1>{props.btitle}</h1>
                        <p className="text-uppercase"><a href="#"><i className="fa fa-home"></i></a> | {props.bpages}</p>
                    </div>
                </div>
            </div>   
        </div>
    )
}

export default Breadcum
