import React from 'react'
import Whybox from './Whybox';
import whyimg from "../assets/img/why/ery.jpg"
const Whychoos = () => {
    return (
        <>
            <div className="why-choos-us">
               <div className="container-fluid">
                   <div className="row align-items-center">
                       <div className="col-lg-6">
                            <div class="common-title">
                                <div class="hero-inner-content">
                                    <h5>//Why Choose Us</h5>
                                    <h1>How Your Business<br/> Going with us</h1>
                                    <div className="desc">
                                        <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy.</p>
                                    </div>
                                </div>
                            </div>

                            <Whybox icon="fa fa-phone" title="Helpdesk Support" desc="Lorem Ipsum is simply dummy text of the printing and typesetting industry."></Whybox>
                            <Whybox icon="fa  fa-lightbulb-o" title="Creative solution" desc="Lorem Ipsum is simply dummy text of the printing and typesetting industry."></Whybox>

                       </div>

                       <div className="col-lg-6">
                           <div className="why-choose-us-thumbnail">
                               <img src={whyimg} alt="Why Choose"/>
                               <div className="why-choose-us-video d-flex align-items-center">
                                   <div className="video-btn">
                                       <a href="#"> <i className="fa fa-play"></i> </a>
                                   </div>
                               </div>
                           </div>
                       </div>
                   </div>
               </div>
           </div> 
        </>
    )
}

export default Whychoos
