import React from 'react'
import Siteinfo from '../components/Siteinfo';
import Usefulllink from '../components/Usefulllink';
import Helplink from '../components/Helplink';
import Newsletter from '../components/Newsletter';
const Footer = () => {
    return (
        <div className="footer-area">
           <div className="footer-top-area">
               <div className="container">
                   <div className="footer-top-outer-row">
                       <div className="row align-items-center">
                           <div className="col-lg-6">
                               <div className="top-left-content">
                                   <p>Start <span>Journey</span> Top Better Business</p>
                               </div>
                           </div>
                           <div className="col-lg-6">
                           <div className="top-right-content text-right">
                                <div class="site-btn">
                                    <a href="#">Get Started<i class="fa fa-angle-right"></i></a>
                                </div>
                           </div>
                           </div>
                       </div>
                   </div>
               </div>
           </div>
           <div className="footer-widget-area">
               <div className="container">
                   <div className="row">
                        <Siteinfo></Siteinfo>
                        <Usefulllink></Usefulllink>
                        <Helplink></Helplink>
                        <Newsletter></Newsletter>
                   </div>
               </div>
           </div>

           <div className="footer-copyright">
               <div className="container">
                   <div className="row">
                       <div className="col-lg-12">
                           <div className="copytext text-center">
                               <p>Copyright @ yourdomain.com</p>
                           </div>
                       </div>
                   </div>
               </div>
           </div>

        </div>
    )
}

export default Footer
